<template>
	<div id="statcenter-takeout">
		<public-header title="数据分析"></public-header>
		<div class="content">
			<div class="total flex-lr">
				<div class="total-item van-hairline--right">
					<div class="font-bold">{{stat.total_num}}</div>
					<div class="font-13 c-gray margin-5-t">今日外卖订单</div>
				</div>
				<div class="total-item">
					<div class="font-bold">¥{{stat.total_price > 0 ? stat.total_price : 0}}</div>
					<div class="font-13 c-gray margin-5-t">今日外卖营业额</div>
				</div>
			</div>
			<van-cell-group class="margin-10-t">
				<van-cell is-link @click="onGetTakeoutSearch('takeout')">
					<div slot="title" class="flex">
						<i class="icon icon-waimai margin-5-r font-20 c-primary"></i>外卖统计
					</div>
				</van-cell>
				<van-cell is-link @click="onGetTakeoutSearch('paytype')">
					<div slot="title" class="flex">
						<i class="icon icon-pay margin-5-r font-18 c-danger"></i>支付方式统计
					</div>
				</van-cell>
				<van-cell is-link @click="onGetTakeoutSearch('orderStore')">
					<div slot="title" class="flex">
						<i class="icon icon-order margin-5-r font-16 c-info"></i>店铺订单统计
					</div>
				</van-cell>
				<van-cell is-link @click="onGetTakeoutSearch('orderChannel')">
					<div slot="title" class="flex">
						<i class="icon icon-viewgallery margin-5-r font-15 c-warning"></i>订单来源统计
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell is-link  @click="onGetTakeoutSearch('deliveryIndex')">
					<div slot="title" class="flex">
						<i class="icon icon-list margin-5-r font-18 c-primary"></i>配送统计
					</div>
				</van-cell>
				<van-cell is-link @click="onGetTakeoutSearch('deliveryDay')">
					<div slot="title" class="flex">
						<i class="icon icon-text margin-5-r font-15 c-danger"></i>配送详情
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell is-link @click="onGetTakeoutSearch('hotGoods')">
					<div slot="title" class="flex">
						<i class="icon icon-goods_light margin-5-r font-18 c-info"></i>热门商品
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell is-link :to="util.getUrl({path: '/pages/statcenter/finance'})">
					<div slot="title" class="flex">
						<i class="icon icon-refund margin-5-r font-18 c-warning"></i>财务统计
					</div>
				</van-cell>
			</van-cell-group>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				searchOptions: {
					takeout: {
						title: '外卖统计搜索',
						key: 'stat_takeout',
						url: 'plateform/statcenter/takeout/search',
						redirctUrl: 'pages/statcenter/takeout'
					},
					paytype: {
						title: '支付方式搜索',
						key: 'stat_paytype',
						url: 'plateform/statcenter/takeout/search',
						redirctUrl: 'pages/statcenter/paytype'
					},
					orderChannel: {
						title: '订单来源统计搜索',
						key: 'stat_orderChannel',
						url: 'plateform/statcenter/takeout/search',
						redirctUrl: 'pages/statcenter/takeoutOrderChannel'
					},
					orderStore: {
						title: '店铺订单统计搜索',
						key: 'stat_orderStore',
						url: 'plateform/statcenter/takeout/search?searchtype=takeoutOrder',
						redirctUrl: 'pages/statcenter/takeoutOrder'
					},
					hotGoods: {
						title: '热门商品统计搜索',
						key: 'stat_hotGoods',
						url: 'plateform/statcenter/takeout/search?searchtype=hotGoods',
						redirctUrl: 'pages/statcenter/hotGoods'
					},
					deliveryIndex: {
						title: '配送统计搜索',
						key: 'stat_delivery',
						url: 'plateform/statcenter/takeout/search?searchtype=deliveryIndex',
						redirctUrl: 'pages/statcenter/deliveryIndex'
					},
					deliveryDay: {
						title: '配送详情搜索',
						key: 'stat_delivery',
						url: 'plateform/statcenter/takeout/search?searchtype=deliveryDay',
						redirctUrl: 'pages/statcenter/deliveryDay'
					},
				}
			}
		},
		methods: {
			...mapMutations([
				'setSearch'
			]),
			onLoad() {
				getData({
					vue: this,
					autoAssign: true,
					variable: 'stat',
					url: 'plateform/statcenter/takeout/index',
				});
			},

			onGetTakeoutSearch(type) {
				var options = this.searchOptions[type];
				options.type = 'filter';
				this.setSearch(options);
				this.$router.push(this.util.getUrl({path: '/pages/common/search'}));
			}
		},
		mounted() {
			this.onLoad();
		},
	}
</script>

<style>
	#statcenter-takeout .total{
		background-color: #fff;
		padding: 15px 0;
	}
	#statcenter-takeout .total .total-item{
		width: 50%;
		text-align: center;
		font-size: 24px;
	}
</style>


